import React, { Component } from 'react';
import { Dialog, Button } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class XDialog extends Component {
	state = {
		dialogVisible: false
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}

	render() {
		const { optionsjs} = this.props.configs

		return (
			<div id="x-dialog">
				<h3>Dialog 对话框</h3>
				<div className="mt-20 mb-20">
					<Button type="text" onClick={() => this.setState({ dialogVisible: true })}>点击打开 Dialog</Button>
					<Dialog
						title="提示"
						size="tiny"
						visible={this.state.dialogVisible}
						onCancel={() => this.setState({ dialogVisible: false })}
						lockScroll={false}
					>
						<Dialog.Body>
							<span>这是一段信息</span>
						</Dialog.Body>
						<Dialog.Footer className="dialog-footer">
							<Button onClick={() => this.setState({ dialogVisible: false })}>取消</Button>
							<Button type="primary" onClick={() => this.setState({ dialogVisible: false })}>确定</Button>
						</Dialog.Footer>
					</Dialog>
				</div>

				<Codemirror value={
					`<Button type="text" onClick={ () => this.setState({ dialogVisible: true }) }>点击打开 Dialog</Button>
<Dialog
	title="提示"
	size="tiny"
	visible={ this.state.dialogVisible }
	onCancel={ () => this.setState({ dialogVisible: false }) }
	lockScroll={ false }
>
	<Dialog.Body>
		<span>这是一段信息</span>
	</Dialog.Body>
	<Dialog.Footer className="dialog-footer">
		<Button onClick={ () => this.setState({ dialogVisible: false }) }>取消</Button>
		<Button type="primary" onClick={ () => this.setState({ dialogVisible: false }) }>确定</Button>
	</Dialog.Footer>
</Dialog>`
				} options={optionsjs} />
			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

})(XDialog)
